* {
  -webkit-box-sizing: padding-box;
          box-sizing: padding-box;
}

html,
body,
#root,
.App {
  display: grid;
  margin: 0;
  height: 100vh;
  width: 100vw;
  width: 100%;
  height: 100%;
  /* max-width: 100%; */
  /* max-height: 100%; */
  overflow: hidden;
}

.App {
  background-color: Window;
  display: grid;
  grid-template:
    [header-left] 'header  ' auto [header-right]
    [main-left] 'main    ' 1fr [main-right]
    [footer-left] 'footer  ' auto [footer-right]
    / 1fr;

  min-height: 100%;
}

.App > header {
  background-color: #369;
  color: #fff;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 20px;
  grid-area: header;
}

.MonacoEditorContainer {
  all: initial;
}

.App > .MarkoutEditorContainer {
  overflow: hidden;
  grid-area: 'main';
  /* contain: style; */
}

.MarkoutEditorContainer > .MonacoEditorContainer {
  /* position: relative; */
}

